import React, { useEffect } from "react";
import styled from "styled-components";
import backimg from "../assets/Images/patrick-tomasso-Oaqk7qqNh_c-unsplash.jpg";
import LogoComponent from "../subComponents/LogoComponent";
import PowerButton from "../subComponents/PowerButton";
import SocialIcons from "../subComponents/Sociallcons";
import { useState } from "react";
import { Blogs } from "../data/BlogData";
import BlogComponent from "./BlogComponent";
import AnchorComponent from "../subComponents/Anchor";
import BigTitle2 from "../subComponents/BigTitle2";
import { motion } from "framer-motion";

const MainContainer = styled(motion.div)`
  background-image: url(${backimg});
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
`;

const Container = styled.div`
  background-color: ${(props) => `rgba(${props.theme.bodyRgba})`};
  width: 100%;
  height: auto;
  position: relative;
  padding-bottom: 5rem;
`;

const Center = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 10rem;
`;

const BlockLine = styled.div`
  display: grid;
  grid-template-columns: repeat(2, minmax(calc(10rem + 15vw), 1fr));
  grid-gap: calc(1rem + 2vw);
  z-index: 5;
`;

//''  ``    !

const BlogPage = () => {
  // "numbers"     0 .
  // "setnumbers"    .
  const [numbers, setnumbers] = useState(0);

  // useEffect        .
  //     ,     .
  useEffect(() => {
    //    70    30 .
    //          .
    const num = (window.innerHeight - 70) / 30;

    //     "numbers"  .
    setnumbers(parseInt(num));
  }, []); //    useEffect     .

  return (
    <MainContainer>
      <Container>
        <LogoComponent />
        <PowerButton />
        <SocialIcons />
        <AnchorComponent numbers={numbers} />

        <Center>
          <BlockLine>
            {Blogs.map((blog, index) => {
              return (
                <motion.div
                  key={blog.id}
                  initial={{ opacity: 0 }}
                  animate={{ opacity: 1 }}
                  transition={{ delay: index * 0.2 }}
                >
                  <BlogComponent blog={blog} />
                </motion.div>
              );
            })}
          </BlockLine>
        </Center>
        <BigTitle2 text="BLOG" top="5rem" left="5rem" />
      </Container>
    </MainContainer>
  );
};

export default BlogPage;
